import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)


export const store = new Vuex.Store({
    state: {
        textSearch:'',
        message:'',
        products: [],
        newProducts: [

        ],
        category: [],
        ratings: [
          {
            id:11,
            rates: 5,
          },
          {
            id:12,
            rates: 4,
          },
          {
            id:13,
            rates: 3,
          },
          {
            id:14,
            rates: 2,
          },
          {
            id:15,
            rates: 1,
          },
        ],
    },
    getters : {
      getMessage : state => state.message,
      getProducts : state => state.products,
      getCartProducts : state => state.newProducts,
      getCategory : state => state.category,
      getRatings : state => state.ratings,
      getTextSearch : state => state.textSearch,

      // getProducts : state => getAllProducts(),
    },
    actions : {
      addTextSearch ({ commit }, data) {
        commit('ADD_TEXT_SEARCH', data)
      },
      addProducts ({ commit }, data) {
        commit('ADD_PRODUCTS', data)
      },
      addCategories ({ commit }, data) {
        commit('ADD_CATEGORIES', data)
      },
      addCart ({ commit }, data) {
        commit('ADD_CART', data)
        commit('GENERATE_MESSAGE')
      },
      removeCart ({ commit }, data) {
        commit('REMOVE_CART', data)
        commit('GENERATE_MESSAGE')
      },
      deleteCart ({ commit }, data) {
        commit('DELETE_CART', data)
        commit('GENERATE_MESSAGE')
      },
      removeAll({commit}){
        commit('REMOVE_ALL')
      },
      addDetailsMessage ({ commit }, data) {
        console.log('AÃ‘ADIENDO DETALLES')
        console.log(data)
        commit('ADD_DETAILS', data)
      },
    },
    mutations: {
      ADD_TEXT_SEARCH (state, data) {
        state.textSearch = data
      },
      ADD_CATEGORIES (state, data) {
        state.category = data
      },
      ADD_PRODUCTS (state, data) {
        state.products = data
      },
      ADD_CART (state, data) {
        let productExist = false;

        state.newProducts = state.newProducts.map(product => {
          if(product._id == data._id){
            productExist = true
            product.cantidadInit += 1
            // state.message = state.message+data;
          }
          return product
        })

        if(!productExist){
          data.cantidadInit += 1
          state.newProducts.push(data);
        }

      },
      REMOVE_CART (state, data) {
        data.cantidadInit -= 1
        console.log(data.cantidadInit);
        if(data.cantidadInit == 0){
          let product = state.newProducts.filter(product => product._id!= data._id)
          state.newProducts = product
        }
      },
      REMOVE_ALL (state){
        state.newProducts = []
      },
      DELETE_CART (state, data) {
        let product = state.newProducts.filter(product => product._id != data._id)
        state.newProducts = product
      },

      GENERATE_MESSAGE(state){
        let doblelineas = '%20%0A%E2%95%90%E2%95%90%E2%95%90%E2%95%90%E2%95%90%E2%95%90%E2%95%90%E2%95%90%E2%95%90%E2%95%90%E2%95%90%E2%95%90%E2%95%90%E2%95%90%E2%95%90%E2%95%90%E2%95%90%E2%95%90%E2%95%90'
        let lineas="%0A--------------------------";
        let total = 0;
        state.message = '';
        for (let i = 0; i < state.newProducts.length; i++) {
          total += state.newProducts[i].precio * state.newProducts[i].cantidadInit
          state.message = state.message + doblelineas+ '%0A%E2%9C%85' + state.newProducts[i].nombre+'%0A%0Aunidades:%20' + state.newProducts[i].cantidadInit+'%0ASubtotal%20=%20Bs%20'+state.newProducts[i].cantidadInit*state.newProducts[i].precio;
        }
        state.message = state.message.replace(/ /g, "%20")+lineas+"%0ATotal%20a%20pagar:%20Bs%20"+total;
      },
        ADD_DETAILS (state, data) {
          state.message = '%F0%9F%91%8B%20Hola%20soy%20' + data.nameClient +'%0ALe%20escribo%20desde%20' + data.Address +'%F0%9F%87%A7%F0%9F%87%B4%0ADeseo%20estos%20productos:'+ state.message
          // state.messate = '%F0%9F%91%8B%20Hola%20soy%'+ data.nameClient +'%20%0ADeseo%20estos%20productos:'
        if(data.comment != ""){
          state.message = state.message +'%0ANOTA:%20'+data.comment.replace(/ /g, "%20")
        }
        if(data.voucher != ""){
          state.message = state.message +'%0AVOUCHER:%20'+data.voucher.replace(/ /g, "%20")
        }

      }

    },

})

