import VueSvgInlinePlugin from 'vue-svg-inline-plugin'
import 'vue-svg-inline-plugin/src/polyfills'
import Vue from 'vue'
import App from './App.vue'
import router from './router'
import vuetify from '@/plugins/vuetify'
import { store } from './store/store'
import axios from 'axios'
// import VueVideoPlayer from 'vue-video-player'
import './plugins'
import '@/assets/scss/_global.scss'
Vue.config.productionTip = false

// plugin svgrender
// import Vue plugin


// import polyfills for IE if you want to support it

// use Vue plugin without options
Vue.use(VueSvgInlinePlugin)
// Vue.use(VueVideoPlayer)

const HTTP = axios.create({
  // baseURL: 'https://artistica-backend.herokuapp.com/api',
  baseURL: 'http://localhost:8080/api',
})
Vue.config.productionTip = false
Vue.prototype.$http = HTTP


// use Vue plugin with options
VueSvgInlinePlugin.install(Vue, {
  attributes: {
    data: ['src'],
    remove: ['alt']
  }
});

// end:: svgrender

import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'



Vue.component(VueSlickCarousel)
Vue.component('VueSlickCarousel', VueSlickCarousel);
import VueApexCharts from 'vue-apexcharts'
Vue.use(VueApexCharts)

Vue.component('apexchartComponent', VueApexCharts)
new Vue({
  router,
  vuetify,
  store,
  render: h => h(App)
}).$mount('#app');
